import scan from '@/components/p-scan/scan.vue'

export default {
	components: {
		scan
	},
	data() {
		return {
			ways: true,
			ordersn: "",
			paytime: "",
			deviceid: "",
			msg: "",
			scan: false
		}
	},
	onLoad() {
		// this.__init()
	},
	methods: {
		changeScan() {
			this.scan = !this.scan
			this.getScanCode
		},
		 //获取扫码控件
		getScanCode(val){
			console.log(val)
			this.ordersn = val
			this.__init()
		},
		change() {
			this.ways = !this.ways
		},
		async __init() {
			this.$H.post("/agent/", {
				user_id: "100003",
				token: "dXQyMDIwMDMyMzExMjM0OTMzNzM3ODAz",
				opt: "device_statistics",
				order_sn: this.ordersn, //始值
				search_pay_time: this.paytime, //数量
				search_device_id: this.deviceid
			}).then((res) => {
				this.msg = res
				try {
				    uni.setStorageSync('remsg', res.arr[0]);
					// uni.setStorageSync('ordersn', res.arr[0].order_sn);
					if(this.msg) {
						console.log('this.msg', this.msg);
						uni.navigateTo({
							url: "/pages/order-refund/order-refund"
						})
					} else {
						uni.showToast({
							title: "输入有误，未找到！",
							duration: 2500
						})
					}
				} catch (e) {
				    // error
					console.log('catch error: ',e);
				}
			}).catch((e) => {
				console.log("catch error!!", e);
			})
			
		}
	}
}
